
import React, { useState, useEffect } from "react";
import axios from "axios";
import './App.css';

const Todo = ({ list, index, complete, remove }) => {
	return (
		<div className="todo" 
		  style={{ textDecoration: list.isCompleted ? "line-through" : "" }}>
      { isEdit ? 
      <div>
		    {list.title}
		    <div>
          <button onClick={() => complete(index)}>Edit</button>
          <button onClick={() => remove(index)}>x</button>
        </div>
      </div> :
      <div>
		    <input>{list.title}</input>
		    <div>
          <button onClick={() => complete(index)}>Edit</button>
          <button onClick={() => remove(index)}>x</button>
        </div>
      </div> }
    </div>
  );

}

function TodoForm({ fff }) {
  const [value, setValue] = useState("");

  const handleSubmit = e => {
    e.preventDefault();
    if (!value) return;
    fff(value);
    setValue("");
  };

  return (
    <form onSubmit={handleSubmit}>
      <input
        type="text"
        className="input"
        value={value}
        onChange={e => setValue(e.target.value)}
      />
    </form>
  );
}

function App() {
  const [todos, setTodos] = useState([{}]);

  useEffect(() => {
    axios.get(`https://jsonplaceholder.typicode.com/posts`)
    .then(res => {
      console.log(res.data);
      setTodos(res.data);
    })
  }, []);

  const addTodo = text => {
    const newTodos = [...todos, { text }];
    setTodos(newTodos);
    console.log(...todos, {text});
    console.log(...todos, {text});
  };

  const completeTodo = index => {
     const newTodos = [...todos];
     newTodos[index].isEdit = true;
    //  setTodos(newTodos);
   };

   const removeTodo = index => {
     const newTodos = [...todos];
     newTodos.splice(index, 1);
     setTodos(newTodos);
   };

  return (
    <div className="app">
      <div className="todo-list">
        {todos.map((r, index) => (
          <Todo
            key={index}
            index={index}
            list={r}
            complete={completeTodo}
            remove={removeTodo}
          />
        ))}
        <TodoForm fff={addTodo} />
      </div>
    </div>
  );
}

export default App;
